.Servicios {
  &__Services {
    background-color: $color-white3;
  }
  &__Wonoma {
    background-image: url(../../assets/services-bg6.jpg);
    background-size: 120%;
    transition: 4s linear;
    will-change: background-position;
    background-position: 35% 89%;
    background-position: -57.5% 89%;
    background-position: -250% 89%;

    display: flex;
    justify-content: space-between;
  }
}

.services-container {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
}

.services-subcontainer {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.services-box {
  width: 31%;
  height: 15vw;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  cursor: default;
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;
  transition: 3s ease-out;
  will-change: background-size;
  background-color: $color-secondary;
  &__1 {
    background-image: url("../../assets/services3.JPG");
    background-position: center 35%;
  }
  &__2 {
    background-image: url("../../assets/services2.jpg");
    background-position: center 50%;
  }
  &__3 {
    background-image: url("../../assets/services3.png");
    background-position: center 30%;
  }
  &:hover {
    background-size: 120%;
    & .services-box-content {
      color: $color-white;
    }
  }
}

.services-box-content {
  width: 85%;
  height: 80%;
  margin: 0;
  position: relative;
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.5s;
  will-change: color;
  border: 2px solid $color-variation;
  color: $color-variation;
}

.wonoma-container {
  width: 100%;
  display: flex;
}

.wonoma-image-container {
  width: 50%;
  height: 50vw;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  background-color: $color-white2;
}

.wonoma-image-subcontainer {
  box-shadow: inset 0 0 85px 0 rgba(0, 0, 0, 0.5);
  position: relative;
  z-index: 2;
  background-position: center;
  background-repeat: no-repeat;
  background-color: $color-variation-dark;
  &__2 {
    background-image: url("../../assets/wonoma1.png");
    height: 25vw;
    background-size: 100%;
    width: 40%;
  }

  &__3 {
    background-image: url("../../assets/wonoma5.jpg");
    height: 50vw;
    background-size: 115%;
    background-position: center right;
    width: 60%;
  }
  &__1 {
    height: 25vw;
    background-size: 100%;
    width: 40%;

    background-image: url("../../assets/wonoma4.png");
  }
}

.wonoma-content-container {
  border: 2px solid red;
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 2;
}

.wonoma-content {
  width: 75%;
  height: 35vw;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2vw;
  border: 2px solid $color-variation-dark;
}
